<template>
    <button
        class="BigButton"
        :type="type"
        :class="computedClass"
        @click="click"
    >
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'BigButton',
    props: {
        borderColor: {
            type: String,
            default: 'white',
        },
        color: {
            type: String,
            default: null,
        },
        type: {
            type: String,
            default: 'button',
        },
        disabled: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        computedClass() {
            return {
                [`border-${this.borderColor}`]: true,
                [this.color]: true,
                disabled: this.disabled,
            };
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';
    @import "@/assets/scss/bulma/projectBulma.scss";

    .BigButton {
        user-select: none;
        font-family: "Roboto", sans-serif;
        padding: 10px 30px;
        min-width: 100px;
        background-color: transparent;
        border: solid 2px $primary;
        border-radius: 3px;
        color: $dark-text;
        font-size: 1em;
        font-weight: bold;
        outline: none;
        cursor: pointer;

        transition: 0.1s ease-out opacity;

        &.border-white {
            border-color: $white;
        }

        &.border-primary {
            background-color: transparent;
            border-color: $primary;
            color: $primary;
        }

        &.border-danger {
            border-color: red;
        }

        &.primary {
            background-color: $primary;
            color: white;
        }

        &.disabled {
            pointer-events: none;
            opacity: 0.2;
        }
    }
</style>
